@use '@/assets/scss/global.scss' as *;
@use '@/assets/scss/deve.scss' as *;
@use '@/assets/scss/variables.scss' as *;

  #article_pre_line{
    font-size: 12px;
    .post-body-title {
      color: rgb(88, 102, 110);
      padding: 8px 15px 6px 15px;
      margin: 10px 15px 0px 15px;
      background-color: white;
      font-size: 14px;
      border: 1px solid rgb(238, 238, 238);
      box-sizing: border-box;
      border-radius: 5px;

      span {
        cursor: pointer;
      }

      .right-area {
        float: right;

        .weibo {
          margin-left: 8px;
          margin-right: 4px;
        }
      }

      .sep {
        font-size: 13px;
        color: #ccc;
        margin-left: 2px;
        margin-right: 2px;
      }

      span {
        vertical-align: top;
      }

      .home {
        margin-right: 2px;
      }
    }
  }
