@use '@/assets/scss/global.scss' as *;
@use '@/assets/scss/deve.scss' as *;
@use '@/assets/scss/variables.scss' as *;

  #article_title {
    .article-page-head-wrap {
      border-bottom: 1px solid #e2e8ea;
      padding: 18px 15px 22px 22px;
      background-color: #fff;

      .author-title {
        font-size: 22px;
        margin-bottom: 15px;
        .fangdabtn{
          margin-left: 10px;
          cursor: pointer;
        }
      }

      .author-sign {
        margin-top: 6px;
        color: rgb(152, 166, 173);
        font-size: 12px;

        > span {
          margin-right: 10px;

          .icon {
            margin-right: 1px;
          }
        }

        .renyuanguanli, .ios-shijian, .see, .label {
          font-size: 11px;
        }

        .label-item {
          cursor: pointer;
        }
      }
    }
  }
