@use '@/assets/scss/global.scss' as *;
@use '@/assets/scss/deve.scss' as *;
@use '@/assets/scss/variables.scss' as *;

  #article_message {
    .article-message-wrap {
      .message-title {
        font-size: 19px;
        color: #34495e;
        font-weight: 600;
        padding-bottom: 10px;
      }
      .message-body-nocomment{
        font-size: 15px;
        text-align: center;
        color: gray;
      }
      .message-body-warp {
        font-size: 15px;

        .body-item {
          vertical-align: top;
          width: 100%;
          position: relative;
          border-top: 1px solid #e7edef;
          padding-top: 12px;
          padding-bottom: 12px;
          box-sizing: border-box;

          .avatar-img {
            display: inline-block;
            padding-right: 10px;
            box-sizing: border-box;
            cursor: pointer;
            img {
              width: 45px;
              height: 45px;
              border-radius: 28px;
              box-shadow: 1px 1px 3px rgba(0, 0, 0, .2);
            }
          }

          .message-desc {

            vertical-align: top;
            display: inline-block;
            word-wrap: break-word;
            width: calc(100% - 60px);
            .message-desc-bar {
              cursor: pointer;
              display: inline-block;
              .desc-level {
                font-size: 13px;
                text-decoration: none;
                color: #333;
              }

              .desc-author {
                margin-left: 5px;
                font-size: 16px;
                display: inline-block;
                font-weight: 600;
                color: #58666e;
              }
            }

            .desc-date {
              font-size: 12px;
              color: rgba(152, 166, 173, .9);
              margin: 8px 0 8px 0;
            }

            .desc-content {
              font-size: 15px;
              color: #536269;
              line-height: 21px;
              margin-top: 5px;
              margin-bottom: 20px;

              a {
                color: #536269;
                text-decoration: none;
              }
              img{
                max-width: 100%;
                max-height: 100%;
              }
            }
          }

          .message-btn-wrap {
            display: inline-block;
            vertical-align: top;
            position: absolute;
            right: 0px;
            width: 150px;
            height: 100%;

            .message-btn {
              width: 100%;

              > span {
                font-size: 9px;
                padding: 2px 8px;
                cursor: pointer;
                border-radius: 10px;
                background-color: rgba(25, 169, 213, .2);
                box-shadow: 1px 1px 3px rgba(0, 0, 0, .2);
                color: rgba(80, 80, 80, .6);
                text-align: center;
                margin-right: 4px;
                text-align: center;
                float: right;
                height: 14px;
                line-height: 14px;
                .icon {
                  position: relative;
                  top: 1px;
                }

                .nozan {
                  display: inline-block;
                  transform: rotate(180deg);
                }
              }
            }

            .btn1 {
              position: absolute;
              top: 0;
            }

            .btn2 {
              position: absolute;
              bottom: 25px;
            }
          }

        }
      }
    }
  }
